.header{
  font-size: 0.16rem;
  width: 100%;
  height: 0.89rem;
  display: flex;
  margin: 0;
  padding: 0 3.67rem;
  box-sizing: border-box;
  font-family: "Microsoft YaHei UI";
  box-shadow: 0rem 0.03rem 0.1rem #FFFFFF;
  background-color: rgb(41,93,150);
  position: fixed;
  top: 0;
  z-index: 1000;
}
.header_img{
  width: 0.89rem;
  height: 100%;
  margin: 0;
  padding: 0;
}
.header_img img{
  margin: 0;
  display: inline-block;
  width: 0.89rem;
  height: 0.89rem;
  padding: 0;
}
.nav{
  display: flex;
  width: 5.84rem;
  height: 0.89rem;
  margin: 0;
  padding: 0;
  align-items: center;
}
.nav li{
  width: 100%;
  height: 0.89rem;
  list-style: none;
  flex: 1;
  text-align: center;
}
.nav li a{
  display: inline-block;
  width: 100%;
  height: 0.89rem;
  line-height: 0.89rem;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}
.active{
  color: #72b8ff;
}
.login{
  width: 3.64993rem;
  text-align: right;
  height: 0.89rem;
  position: absolute;
  top: 0;
  right: 3.67rem;
}
.login .warp{
  padding: 0.24rem 0;
  height: 0.89rem;
  box-sizing: border-box;
}
.login .warp a{
  text-decoration: none;
  display: inline-block;
  height: 0.41rem;
  width: 1.19rem;
  color: #fff;
  font-weight: bold;
  line-height: 0.41rem;
  text-align: center;
  border: 0.02rem solid rgb(66,140,215);
}
.login .warp a:first-child{
  background: rgb(66,140,215);
  border-right:none;
}
