.about{
  width: 100%;
  padding: 0 3.67rem ;
  height: 8.25rem;
  box-sizing: border-box;
  background-color: rgb(42,93,148);
  display: flex;
  font-size: 0.16rem;
  font-family: "Microsoft YaHei UI";
  color: #ffffff;
  align-items: center;
  font-family: "Microsoft YaHei UI";
}
.about div{
  flex: 1;
}
.about .left h2{
  font-size: 0.57rem;
  margin: 0 0 0.2rem 0;
}
.about .left{
  width: 100%;
  height: 4.94rem;
  overflow: hidden;
}
.about .left .left_line{
  margin: 0;
  width: 1.34rem;
  height: 0.02rem;
  background-color: #ffffff;
}
.about .left p{
  text-indent: 0.4rem;
  line-height: 0.4rem;
}
.about .left .sell{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.left .sell span{
  padding: 0 0.3rem;
  border: 0.01rem solid #ffffff;
  height: 0.41rem;
  line-height: 0.41rem;
  font-size: 0.14rem;
  margin-right: 0.08rem;
  margin-top: 0.08rem;
}
.about .center{
  width: 100%;
  padding: 0 0.35rem;
  height: 4.94rem;
  overflow: hidden;
}
.about .center .center_first{
  margin-top: 0;
}
.about .center p{
  width: 100%;
  line-height: 0.4rem;
  text-indent: 0.4rem;
}
.about .center a{
  text-align: right;
  text-decoration: none;
  width: 100%;
  display: inline-block;
  color: #ffffff;
}
.about .right{
  width: 100%;
  height: 4.94rem;
  text-align: right;
}
.about .right .right-warp{
  width: 3.4rem;
  background: #fff;
  display: inline-block;
}
.about .right .right_top{
  width: 100%;
  height: 1.32rem;
  text-align: right;
}
.about .right .right_top img{
  width: 100%;
  height: 1.32rem;
  display: inline-block;
}
.about .right_center{
  width: 100%;
  text-align: right;
  background: #fff;
  display: flex;
  flex-wrap: nowrap;
}


.about .right_center img{
  height: 0.63rem;
  width: 1.7rem;
  margin: 0;

}
.about .right .right_bottom img{
  display: inline-block;
  width: 3.16rem;
}
.about .right .right_bottom {
  width: 100%;
  padding: 0.1rem 0.12rem 0.11rem 0;
  background: #fff;
  box-sizing: border-box;
}
