.alert{
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  top: 0;
}
.alertWarp {
  margin-top: 1.4rem;
  width: 8.75rem;
  height: 6.93rem ;
  background: #fff;
}
.alertWarp .headers{
  width: 100%;
  height: 0.8rem;
  display: flex;
  font-size: 0.28rem;
  font-family: "Microsoft YaHei UI";
  justify-content: space-between;
  background: rgb(41,93,150);
}
.alertWarp .headers .headerTitle{
  height: 0.8rem;
  line-height: 0.8rem;
  color: #fff;
  padding-left: 0.56rem;
}
.alertWarp .headers .headerClose{
  width: 0.8rem;
  height: 0.8rem;
  cursor: pointer;
}
.alertWarp .headers .headerClose img{
  display: inline-block;
  width: 0.8rem;
  height: 0.8rem;
}
.alertWarp .form{
  padding-top: 0.2rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  font-size: 0.16rem;
  height: 4.43rem;
  font-family: "Microsoft YaHei UI";
  /*height: ;*/
}
.alertWarp .form .required{
  padding-right: 1.28rem;
  box-sizing: border-box;
  margin: 0;
  width: 6.19rem;
  text-align: right;
  height: 0.69rem;
  display: flex;
  align-items: center;
}

.alertWarp .form .required label{
  display: inline-block;
    width: 1rem;
  text-align: right;
  color: #285d95;
  margin-right: 0.32rem;
}
.alertWarp .form .required .inputs{
  color: #3e3e3e;
  display: inline-block;
  width: 1rem;
  text-align: right;
  margin-right: 0.32rem;
}
.alertWarp .form .input{
  width: 3.5rem;
  height: 0.39rem;
  text-indent: 0.2rem;
}
.alertWarp .form .checkbox{
  padding-right: 0;
  box-sizing: border-box;
  margin-right: 0.32rem;
  display: flex;
  align-items: center;
  width: 6.19rem;
  height: 0.39rem;
}
.label{
  display: inline-block;
  width: 1rem;
  text-align: right;
  color: #285d95;
  margin-right: 0.32rem;
}
.checwarp{
  width: 4.78rem;
  display: flex;
  overflow: hidden;
}
.checwarp label{
  flex: 1;
  display: flex;
  align-items: center;
  margin: 0;
}
.alertWarp .form .scale{
  padding-right: 1.2rem;
  box-sizing: border-box;
  margin: 0;
  width: 6.19rem;
  height: 0.39rem;
  display: flex;
  align-items: center;
}
.alertWarp .form .scale .scaled{
  width: 3.5rem;
  display: flex;
  padding-right: 0;
  white-space: nowrap;
}
.alertWarp .form .scale .scaled input{
  width: 0.2rem;
  height: 0.2rem;
}
.alertWarp .form .scale .scaled .scaledd{
  flex: 1;
  display: flex;
  align-items: center;
}

.footer-footer{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  height: 1.7rem;
  background: rgb(242,242,242);
  font-size: 0.12rem;
  font-family: "Microsoft YaHei UI";
}
.footer-footer div{
  width: 100%;
}
.footer-footer .statement{
  width: 100%;
  margin: 0;
  margin-top: 0.2rem;
  height: 0.5rem;
  display: flex;
  justify-content:center;
}
.footer-footer .statement p{
    color: #939393;
   width: 6.19rem;
   margin: 0;
  height: 0.5rem;
  line-height: 0.25rem;
 }
.footer-footer .statement p span{
 color: #285d95;
}
.footer-footer .submit{
  width: 100%;
  margin: 0rem 0 0.5rem 0;
  display: flex;
  justify-content:center;
}
.footer-footer .submit{
  width: 100%;
  margin: 0rem 0 0.1rem 0;
  height: 0.7rem;
  display: flex;
  justify-content:center;
}
.footer-footer .submit button{
  width: 6.19rem;
  margin: 0;
  height: 0.7rem;
  font-size: 0.24rem;
  background: rgb(67,141,216);
  cursor: pointer;
  color: #ffffff;
}
