body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body,html{
  scroll-behavior: smooth;
}
.normal {
  font-family: Georgia, sans-serif;
  margin: 0;
  padding: 0;
}

.header{
  font-size: 0.16rem;
  width: 100%;
  height: 0.89rem;
  display: flex;
  margin: 0;
  padding: 0 3.67rem;
  box-sizing: border-box;
  font-family: "Microsoft YaHei UI";
  box-shadow: 0rem 0.03rem 0.1rem #FFFFFF;
  background-color: rgb(41,93,150);
  position: fixed;
  top: 0;
  z-index: 1000;
}
.header_img{
  width: 0.89rem;
  height: 100%;
  margin: 0;
  padding: 0;
}
.header_img img{
  margin: 0;
  display: inline-block;
  width: 0.89rem;
  height: 0.89rem;
  padding: 0;
}
.nav{
  display: flex;
  width: 5.84rem;
  height: 0.89rem;
  margin: 0;
  padding: 0;
  align-items: center;
}
.nav li{
  width: 100%;
  height: 0.89rem;
  list-style: none;
  flex: 1 1;
  text-align: center;
}
.nav li a{
  display: inline-block;
  width: 100%;
  height: 0.89rem;
  line-height: 0.89rem;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}
.active{
  color: #72b8ff;
}
.login{
  width: 3.64993rem;
  text-align: right;
  height: 0.89rem;
  position: absolute;
  top: 0;
  right: 3.67rem;
}
.login .warp{
  padding: 0.24rem 0;
  height: 0.89rem;
  box-sizing: border-box;
}
.login .warp a{
  text-decoration: none;
  display: inline-block;
  height: 0.41rem;
  width: 1.19rem;
  color: #fff;
  font-weight: bold;
  line-height: 0.41rem;
  text-align: center;
  border: 0.02rem solid rgb(66,140,215);
}
.login .warp a:first-child{
  background: rgb(66,140,215);
  border-right:none;
}

.banner{
  width: 100%;
  height: 8.25rem;
  overflow: hidden;
  position: relative;
}
.banner .banner_pic{
  position: fixed;
  width: 100%;
  /*top: -0.89rem;*/
  left: 0;
  /*height: 8.25rem;*/
  z-index: -100;

}
.banner  .banner_tm{
  display: inline-block;
  width: 100%;
  position: absolute;
  /*z-index: 1000;*/
  top:1rem;
  margin: 0;
}

.about{
  width: 100%;
  padding: 0 3.67rem ;
  height: 8.25rem;
  box-sizing: border-box;
  background-color: rgb(42,93,148);
  display: flex;
  font-size: 0.16rem;
  font-family: "Microsoft YaHei UI";
  color: #ffffff;
  align-items: center;
  font-family: "Microsoft YaHei UI";
}
.about div{
  flex: 1 1;
}
.about .left h2{
  font-size: 0.57rem;
  margin: 0 0 0.2rem 0;
}
.about .left{
  width: 100%;
  height: 4.94rem;
  overflow: hidden;
}
.about .left .left_line{
  margin: 0;
  width: 1.34rem;
  height: 0.02rem;
  background-color: #ffffff;
}
.about .left p{
  text-indent: 0.4rem;
  line-height: 0.4rem;
}
.about .left .sell{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.left .sell span{
  padding: 0 0.3rem;
  border: 0.01rem solid #ffffff;
  height: 0.41rem;
  line-height: 0.41rem;
  font-size: 0.14rem;
  margin-right: 0.08rem;
  margin-top: 0.08rem;
}
.about .center{
  width: 100%;
  padding: 0 0.35rem;
  height: 4.94rem;
  overflow: hidden;
}
.about .center .center_first{
  margin-top: 0;
}
.about .center p{
  width: 100%;
  line-height: 0.4rem;
  text-indent: 0.4rem;
}
.about .center a{
  text-align: right;
  text-decoration: none;
  width: 100%;
  display: inline-block;
  color: #ffffff;
}
.about .right{
  width: 100%;
  height: 4.94rem;
  text-align: right;
}
.about .right .right-warp{
  width: 3.4rem;
  background: #fff;
  display: inline-block;
}
.about .right .right_top{
  width: 100%;
  height: 1.32rem;
  text-align: right;
}
.about .right .right_top img{
  width: 100%;
  height: 1.32rem;
  display: inline-block;
}
.about .right_center{
  width: 100%;
  text-align: right;
  background: #fff;
  display: flex;
  flex-wrap: nowrap;
}


.about .right_center img{
  height: 0.63rem;
  width: 1.7rem;
  margin: 0;

}
.about .right .right_bottom img{
  display: inline-block;
  width: 3.16rem;
}
.about .right .right_bottom {
  width: 100%;
  padding: 0.1rem 0.12rem 0.11rem 0;
  background: #fff;
  box-sizing: border-box;
}

.panner{
  width: 100%;
  padding: 0 3.67rem;
  height: 8.25rem;
  background: url(/static/media/bg_3.9d89d3df.jpg);
  background-repeat: no-repeat;
  z-index: 100;
  background-size: 100% 100%;
  box-sizing: border-box;
  display: flex;
  color: #fff;
  justify-content: space-between;
  align-items: center;
}
.panner .panner_left{
  width: 33%;
  box-sizing: border-box;
  padding: 0.7rem 0;
}
.panner .panner_left h2{
  font-size: 0.57rem;
  font-family: "Microsoft YaHei UI";
  margin: 0;
}
.panner .panner_left .left_line{
  margin: 0;
  width: 1.34rem;
  height: 0.02rem;
  background-color: #ffffff;
  margin-top:0.2rem;
}
.panner .panner_left p{
  text-indent: 0.4rem;
  line-height: 0.4rem;
  font-size: 0.16rem;
}
.panner .panner_right{
  width: 66.66%;
  height: 4.67rem;
  display: flex;
  flex-direction:row-reverse;
}
.panner .panner_right_left{
  display: inline-block;
  width: 4.72rem;
  display: flex;
  flex-wrap: wrap;
}
.panner .panner_right_left img{
  width:2.31rem ;
  height: 2.29rem;
  margin: 0.025rem;
}
.panner .panner_right .panner_right_right{
  display: inline-block;
  width: 2.31rem;
  height: 4.67rem;
  margin-left: 0.02rem;
}
.panner_right .panner_right_right img{
  display: inline-block;
  width: 2.31rem;
  height: 4.67rem;
}
