.footers{
  width: 100%;
  box-sizing: border-box;
  padding: 1.79rem 3.67rem;
  height: 8.25rem;
  display: flex;
  color: #FFF;
  align-items: flex-end;
  background: rgb(42,93,148);
}
.footers .footer_left {
  width: 34%;
}
.footers .footer_left h2{
  margin: 0 0 0.2rem 0;
  font-size: 0.57rem;
  font-family: "Microsoft YaHei UI";
}

.footers .footer_left p{
  font-family: "Microsoft YaHei UI";
  font-size: 0.16rem;
  white-space: nowrap;
}
.footers .footer_left_log{
  background: url(data:image/png;base64,);
  background-repeat: no-repeat;
  height: 0.33rem;
  line-height: 0.33rem;
  background-size:0.25rem 0.29rem ;
  background-position: left;
  text-indent: 0.3rem;
}
.footers .footer_left .fot_banquan{
  font-size: 0.12rem;
}
.footers .footer_left .footer_wx{
  margin-top: 0.4rem;
  width: 100%;
  height: 0.99rem;
  line-height: 0.99rem;
}
.footer_left_undline{
  margin: 0.24rem 0;
  margin-top: 0;
  width: 1.34rem;
  height: 0.02rem;
  background: #fff;
}
.footer_left .footer_wx img{
  display: inline-block;
  width: 1rem;
  height: 0.99rem;
}
.footers .footer_right{
  width: 67%;
  height: 3.11rem;
  text-align: right;
}
.footers .footer_right img{
  display: inline-block;
  width: 6.53rem;
  height: 3.11rem;
}

.alert{
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  top: 0;
}
.alertWarp {
  margin-top: 1.4rem;
  width: 8.75rem;
  height: 6.93rem ;
  background: #fff;
}
.alertWarp .headers{
  width: 100%;
  height: 0.8rem;
  display: flex;
  font-size: 0.28rem;
  font-family: "Microsoft YaHei UI";
  justify-content: space-between;
  background: rgb(41,93,150);
}
.alertWarp .headers .headerTitle{
  height: 0.8rem;
  line-height: 0.8rem;
  color: #fff;
  padding-left: 0.56rem;
}
.alertWarp .headers .headerClose{
  width: 0.8rem;
  height: 0.8rem;
  cursor: pointer;
}
.alertWarp .headers .headerClose img{
  display: inline-block;
  width: 0.8rem;
  height: 0.8rem;
}
.alertWarp .form{
  padding-top: 0.2rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  font-size: 0.16rem;
  height: 4.43rem;
  font-family: "Microsoft YaHei UI";
  /*height: ;*/
}
.alertWarp .form .required{
  padding-right: 1.28rem;
  box-sizing: border-box;
  margin: 0;
  width: 6.19rem;
  text-align: right;
  height: 0.69rem;
  display: flex;
  align-items: center;
}

.alertWarp .form .required label{
  display: inline-block;
    width: 1rem;
  text-align: right;
  color: #285d95;
  margin-right: 0.32rem;
}
.alertWarp .form .required .inputs{
  color: #3e3e3e;
  display: inline-block;
  width: 1rem;
  text-align: right;
  margin-right: 0.32rem;
}
.alertWarp .form .input{
  width: 3.5rem;
  height: 0.39rem;
  text-indent: 0.2rem;
}
.alertWarp .form .checkbox{
  padding-right: 0;
  box-sizing: border-box;
  margin-right: 0.32rem;
  display: flex;
  align-items: center;
  width: 6.19rem;
  height: 0.39rem;
}
.label{
  display: inline-block;
  width: 1rem;
  text-align: right;
  color: #285d95;
  margin-right: 0.32rem;
}
.checwarp{
  width: 4.78rem;
  display: flex;
  overflow: hidden;
}
.checwarp label{
  flex: 1 1;
  display: flex;
  align-items: center;
  margin: 0;
}
.alertWarp .form .scale{
  padding-right: 1.2rem;
  box-sizing: border-box;
  margin: 0;
  width: 6.19rem;
  height: 0.39rem;
  display: flex;
  align-items: center;
}
.alertWarp .form .scale .scaled{
  width: 3.5rem;
  display: flex;
  padding-right: 0;
  white-space: nowrap;
}
.alertWarp .form .scale .scaled input{
  width: 0.2rem;
  height: 0.2rem;
}
.alertWarp .form .scale .scaled .scaledd{
  flex: 1 1;
  display: flex;
  align-items: center;
}

.footer-footer{
