.panner{
  width: 100%;
  padding: 0 3.67rem;
  height: 8.25rem;
  background: url('../../assets/bg_3.jpg');
  background-repeat: no-repeat;
  z-index: 100;
  background-size: 100% 100%;
  box-sizing: border-box;
  display: flex;
  color: #fff;
  justify-content: space-between;
  align-items: center;
}
.panner .panner_left{
  width: 33%;
  box-sizing: border-box;
  padding: 0.7rem 0;
}
.panner .panner_left h2{
  font-size: 0.57rem;
  font-family: "Microsoft YaHei UI";
  margin: 0;
}
.panner .panner_left .left_line{
  margin: 0;
  width: 1.34rem;
  height: 0.02rem;
  background-color: #ffffff;
  margin-top:0.2rem;
}
.panner .panner_left p{
  text-indent: 0.4rem;
  line-height: 0.4rem;
  font-size: 0.16rem;
}
.panner .panner_right{
  width: 66.66%;
  height: 4.67rem;
  display: flex;
  flex-direction:row-reverse;
}
.panner .panner_right_left{
  display: inline-block;
  width: 4.72rem;
  display: flex;
  flex-wrap: wrap;
}
.panner .panner_right_left img{
  width:2.31rem ;
  height: 2.29rem;
  margin: 0.025rem;
}
.panner .panner_right .panner_right_right{
  display: inline-block;
  width: 2.31rem;
  height: 4.67rem;
  margin-left: 0.02rem;
}
.panner_right .panner_right_right img{
  display: inline-block;
  width: 2.31rem;
  height: 4.67rem;
}
