.footers{
  width: 100%;
  box-sizing: border-box;
  padding: 1.79rem 3.67rem;
  height: 8.25rem;
  display: flex;
  color: #FFF;
  align-items: flex-end;
  background: rgb(42,93,148);
}
.footers .footer_left {
  width: 34%;
}
.footers .footer_left h2{
  margin: 0 0 0.2rem 0;
  font-size: 0.57rem;
  font-family: "Microsoft YaHei UI";
}

.footers .footer_left p{
  font-family: "Microsoft YaHei UI";
  font-size: 0.16rem;
  white-space: nowrap;
}
.footers .footer_left_log{
  background: url("../../assets/btn_lxwm.png");
  background-repeat: no-repeat;
  height: 0.33rem;
  line-height: 0.33rem;
  background-size:0.25rem 0.29rem ;
  background-position: left;
  text-indent: 0.3rem;
}
.footers .footer_left .fot_banquan{
  font-size: 0.12rem;
}
.footers .footer_left .footer_wx{
  margin-top: 0.4rem;
  width: 100%;
  height: 0.99rem;
  line-height: 0.99rem;
}
.footer_left_undline{
  margin: 0.24rem 0;
  margin-top: 0;
  width: 1.34rem;
  height: 0.02rem;
  background: #fff;
}
.footer_left .footer_wx img{
  display: inline-block;
  width: 1rem;
  height: 0.99rem;
}
.footers .footer_right{
  width: 67%;
  height: 3.11rem;
  text-align: right;
}
.footers .footer_right img{
  display: inline-block;
  width: 6.53rem;
  height: 3.11rem;
}
